import React from 'react'

import R from 'ramda'
import IngredientsListWritten from '../IngredientsListWritten/'

import './index.css'

const MenuWritten = props => {
  const { dishes } = props

  const dishesBySize = R.groupBy(dish => dish.size)(dishes)

  const sizesElements = Object.keys(dishesBySize).map((e, i) => {
    const size = dishesBySize[e][0].size
    const title = dishesBySize[e][0].name
    const sub = dishesBySize[e][0].description
    const dishesElements = dishesBySize[e].map((e, i) => (
        <IngredientsListWritten ingredients={e.ingredients} key={i} />
    ))

    return (
      <div className='MenuWritten__size-group' key={i}>
        <h1 className='MenuWritten__title'>
          {title}
        </h1>
        <h2 className='MenuWritten__subtitle'>
          {sub} con:
        </h2>
        <div className={`MenuWritten__dishes MenuWritten__dishes--${size}`}>
          {dishesElements}
        </div>
      </div>
    )
  })

  return (
    <div className='MenuWritten'>
      {sizesElements}
    </div>
  )
}

export default MenuWritten



