import React from 'react'

import './index.css'

const Price = props => {
  const symbol = props.symbol || '$'

  const price = props.price || '0'
  const strPrice = price.toString()
  const bigNumber = strPrice.slice(0, 1)
  const smallNumbers = strPrice.slice(1)

  return (
    <span className='Price'>
      <span className='Price__symbol'>{symbol}</span>
      <span className='Price__big-number'>{bigNumber}</span>
      ,
      <span className='Price__small-number'>{smallNumbers}</span>
    </span>
  )
}

export default Price



