import React from 'react'

import Dish from '../Dish/'

import './index.css'

const Menu = props => {
  const { dishes } = props
  const dishesElements = dishes.map(e => (
    <li key={e.id}>
      <Dish dish={e} />
    </li>
  ))

  return (
    <ul className='Menu'>
      {dishesElements}
    </ul>
  )
}

export default Menu



