import React from 'react'

import './index.css'

const Ingredient = props => {
  const { ingredient, primary } = props

  let className = 'Ingredient'
  if (primary) {
    className += ' Ingredient--primary'
  }

  return (
    <span className={className}>
      {
        ingredient.amount === 1
        ||
        <span className='Ingredient__amount'>
          {ingredient.amount}
        </span>
      }
      <span className='Ingredient__name'>
        {ingredient.name}
      </span>
    </span>
  )
}

export default Ingredient



