import React from 'react'

import Ingredient from '../Ingredient/'

import './index.css'

const IngredientsListWritten = props => {
  const { ingredients } = props

  const ingredientsPrimary = ingredients.primary || []
  const ingredientsSecondary = ingredients.secondary || []

  const ingredientsPrimaryElements = ingredientsPrimary.map((e, i) => (
    <Ingredient ingredient={e} primary key={i} />
  ))

  const ingredientsSecondaryElements = ingredientsSecondary.map((e, i) => (
    <Ingredient ingredient={e} key={i} />
  ))

  return (
    <div className='IngredientsListWritten'>
      {ingredientsPrimaryElements}
      {ingredientsSecondaryElements}
    </div>
  )
}

export default IngredientsListWritten



