import React from 'react'

import IngredientsList from '../IngredientsList/'
import Price from '../Price/'

import './index.css'
import PicnicBlanketRed from './picnic-blanket-red.png'

const pinicStyle = {
  backgroundImage: `url(${PicnicBlanketRed})`
}

const Dish = props => {
  const { dish } = props

  const newLine = '%0A'
  const api = 'https://api.whatsapp.com/send?phone=573216202901&text='
  const greetings = 'Hola, Yo Quiero!'
  const link = `
${api}${greetings}
${newLine}
${dish.name}
${newLine}
con:
${newLine}
${dish.ingredients.primary[0].amount} ${dish.ingredients.primary[0].name}
${newLine}
`

  return (
    <div className='Dish'>
      <div className='Dish__header'>
        <h1 className='Dish__name'>{dish.name}</h1>
        <h1 className='Dish__subtitle'>{dish.description} con:</h1>
      </div>
      <div className='Dish__body'>
        <IngredientsList ingredients={dish.ingredients} />
        <a className='Dish__link-buy' href={link}>
          Yo Quiero!
        </a>
      </div>
      <div
        style={pinicStyle}
        className='Dish__footer'
      >
        <div className='Dish__price-wrapper'>
          <Price price={dish.price} />
        </div>
      </div>
    </div>
  )
}

export default Dish



