import React from 'react'

import './index.css'
import logo from './potatas-logo.png'

const Header = props => {
  return (
    <header className='Header'>
      <div className='Header__logo-wrapper'>
        <img src={logo} alt='Potatas' />
        <span className='Header__slogan'>
          Yo Quiero!
        </span>
      </div>
    </header>
  )
}

export default Header



